import { Component, OnInit } from '@angular/core';
import { Amount } from './domain/amount';
import { AmountService } from './services/amount.service';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

    displayDialog: boolean;

    data: any;

    dataArray: Amount[];

    cols: any[];

    items: any[]

    constructor(private amountService: AmountService) { }

    ngOnInit() {
        this.amountService.get().subscribe((data) => {
            this.dataArray = data.data
            // console.log(this.dataArray)
            // console.log([...this.dataArray].map(item => Object.keys(item)))
            console.log([...this.dataArray].map(obj => console.log(obj.item)))
            this.data = {
                labels: this.getArray(this.dataArray, 'item'),
                datasets: [
                    {
                        data: this.getArray(this.dataArray, 'amount'),
                        backgroundColor: this.getRandomColor(this.dataArray.length),
                        hoverBackgroundColor: this.getRandomColor(this.dataArray.length)
                    }]
            };
        })
        this.cols = [
            { field: 'item', header: 'item' },
            { field: 'amount', header: 'amount' }
        ];
    }
    getArray(dataArray, col) {
        let array = [];
        [...dataArray].map(obj => array.push(obj[col]))
        return array;
    }
    getRandomColor(length) {
        let array = [];
        for (let i = 0; i < length; i++) {
            let randomColor: string = "#000000".replace(/0/g, function () { return (~~(Math.random() * 16)).toString(16); });
            array.push(randomColor);
        }
        return array;
    }

}
